import React, { useEffect, useState } from "react";
import { getCoinonItems } from "../api/coinone";

function CryptoCoinone() {
  const [data, setData] = useState();

  useEffect(() => {
    getCoinonItems().then((res) => {
      setData(res);
    });
  });

  return (
    <>
      <div>CryptoCoinone</div>

      {data}
    </>
  );
}

export default CryptoCoinone;
